import { NavLink, Link, useNavigate } from "react-router-dom";
import { useState } from "react";
import { motion } from "framer-motion";
import { useDispatch, useSelector } from "react-redux";
import * as actions from '../store/index';
import Navlinks from "../Data/Navlinks";

const Navbar = ({ theme, setTheme }) => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const isLoggedIn = useSelector(state => state.auth.isLoggedIn);
    const [menu, setMenu] = useState("false");

    const menuToggler = () => {
        setMenu(!menu);
    }

    const themeToggler = () => {
        theme === "light" ? setTheme("dark") : setTheme("light");
    };
    const handleLogout = () => {
        localStorage.removeItem('token');
        dispatch(actions.setAuthToken(null));
        navigate('/login');
    };
    return (
        <nav className="navbar navbar-expand-lg">
            <div className="container">
                <Link className="navbar-brand" to="/">Brand.</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation" onClick={menuToggler}>
                    <i className={menu ? "fas fa-bars" : "fas fa-times"} />
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        {Navlinks && Navlinks.map((n) =>
                            <NavLink key={n.id} to={n.path} exact activeClassName="selected">
                                <motion.li
                                    whileHover={{ scale: 1.2 }}
                                    whileTap={{ scale: 0.9 }}
                                    className="nav-item"
                                >{n.title}</motion.li>
                            </NavLink>
                        )}
                        {
                            isLoggedIn ?
                                <>
                                    <NavLink to="/dashboard" exact activeClassName="selected">
                                        <motion.li
                                            whileHover={{ scale: 1.2 }}
                                            whileTap={{ scale: 0.9 }}
                                            className="nav-item"
                                        >Dashboard</motion.li>
                                    </NavLink>
                                    <motion.div
                                        className="mode"
                                        onClick={handleLogout}
                                        whileHover={{ scale: 1.3 }}
                                        whileTap={{ scale: 0.8 }}>
                                        <i class="fas fa-sign-out-alt" />
                                    </motion.div>
                                </>
                                :
                                null
                        }
                        <motion.div
                            className="mode"
                            onClick={themeToggler}
                            whileHover={{ scale: 1.3 }}
                            whileTap={{ scale: 0.8 }}>
                            {theme === "light" ? <i className="far fa-moon" /> : <i className="far fa-sun" />}
                        </motion.div>
                    </ul>
                </div>
            </div >
        </nav >
    )
}

export default Navbar;