import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
// import loginImg from '../assets/login.jpg';
import * as actions from '../store/index';

const Login = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const isLoggedIn = useSelector(state => state.auth.isLoggedIn);
    const [error, setError] = useState(false);
    const [formData, setFormData] = useState({
        username: '',
        password: '',
    })
    const users = [
        { username: 'kunal', password: 'password', name: 'Kunal Srivastava' },
    ];
    const handleSubmit = (e) => {
        e.preventDefault();
        let validUser = users.find(user => user.username === formData.username && user.password === formData.password);
        if (validUser) {
            let userCopy = JSON.parse(JSON.stringify(validUser));
            delete userCopy.password;
            localStorage.setItem('token', JSON.stringify(userCopy));
            dispatch(actions.setAuthToken(userCopy));
            navigate('/dashboard');
        } else {
            setError(true);
        }
    };
    const handleChange = (e) => {
        setFormData(state => ({
            ...state,
            [e.target.id]: e.target.value,
        }))
    };
    useEffect(() => {
        if (isLoggedIn) {
            navigate('/dashboard');
        }
    })
    return (
        <div className="login">
            <h1 className="head">Login</h1>
            <div className="main">
                <div className="container">
                    {error &&
                        <div class="alert alert-danger" role="alert">
                            <strong>Wrong Credits</strong>
                        </div>
                    }
                    <div className="login-fields">
                        {/* <div className="text-center">
                            <img src={loginImg} class="card-img-top" alt="..." />
                        </div> */}
                        <form onSubmit={handleSubmit}>
                            <input
                                id="username"
                                type='text'
                                className="my-1 shadow-none"
                                placeholder="Username"
                                value={formData.username}
                                onChange={handleChange} />
                            <input
                                id="password"
                                type='password'
                                className="my-1 shadow-none"
                                placeholder="Password"
                                value={formData.password}
                                onChange={handleChange} />
                            <button className="btn btn-outline-primary">Submit</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Login;