import notfound from '../notfound.png';


const NotFound = () => {
    return (
        <div className="home">
            <h1 className="head">Page Not Found</h1>
            <p className="text-center">Redirecting to home page.....</p>
            <div className='text-center not'>
                <img src={notfound} alt="#" />
            </div>
        </div>
    )
}

export default NotFound;