import * as actions from '../actionTypes';

const defaultState = {
    token: null,
    isLoggedIn: false,
};

const setAuthToken = (state, action) => {
    return {
        ...state,
        token: action.token,
        isLoggedIn: action.token != null,
    }
}

const authReducer = (state = defaultState, action) => {
    switch (action.type) {
        case actions.SET_AUTH_TOKEN: return setAuthToken(state, action);
        default: return state;
    }
};

export default authReducer;